const description = `<p>This is a series of images that have been “glitched” through various programs in order to give them an abstract and technological feeling. These works were heavily inspired by <strong><a rel='noopener noreferrer' target='_blank' href="https://michaelbetancourt.com/">Micheal Betancourt</a></strong>, who uses Glitch Art to emphasize the connections between media theory and practice. I wanted to create these as a way to showcase the idea of beauty in destruction, or beauty in chaos, specifically in a technological setting.</p>

<p>The images were all created by importing image files into programs that do not traditionally accept them. Values were then changed and they were exported as images again to give me the glitch effect with some of the key programs used being Audacity and Premiere Pro. Having the images being visible through the glitches themselves was also intentional as a way to still illustrate the underlying, original product that I had then warped.</p>

<hr/>`
export default description
